<template>
   <div class="d-row" :style="rowStyl">
       <slot></slot>
   </div>
</template>

<script>
export default {
    name: 'd-row',
    props: {
        gutter: {
            type: Number,
            default: 0
        },
        justify: {
            type: String,
            validator(value) {
                if (value && !['start', 'end', 'center', 'space-around', 'space-between'].includes(value)) {
                    console.error('justify' +['start', 'end', 'center', 'space-around', 'space-between'].join('') + '' );
                }
                return true;
            }
        }
    },
    mounted() {
        this.$children.forEach(child => {
            // col
            child.gutter = this.gutter;// gutter
        });
    },
    computed: {
        rowStyl() {
            let style = {};
            if (this.gutter) {
                style = {
                    ...style,
                    marginLeft: -this.gutter/2 + 'px',// marginpadding, rowgutter,rowoverflow: hidden;
                    marginRight:  -this.gutter/2 + 'px'
                }
            }
            if (this.justify) {
                let key = ['start', 'end'].includes(this.justify) ? 'flex-' + this.justify : this.justify;
                style = {
                    ...style,
                    justifyContent: key
                }
            }
            return style;
        }
    }
}
</script>

<style lang="scss">
.d-row {
    display: flex;
    flex-wrap: wrap;
}
</style>
